  width: 8.3333333333%;
}

.h-2 {
  width: 16.6666666667%;
}

.h-3 {
  width: 25%;
}

.h-4 {
  width: 33.3333333333%;
}

.h-5 {
  width: 41.6666666667%;
}

.h-6 {
  width: 50%;
}

.h-7 {
  width: 58.3333333333%;
}

.h-8 {
  width: 66.6666666667%;
}

.h-9 {
  width: 75%;
}

.h-10 {
  width: 83.3333333333%;
